















































import { Component, Prop, Vue } from 'vue-property-decorator';
import { storage } from '../services';
import { error } from 'util';
import { Card } from '@/services/storage/storage';
import { ObjectUnsubscribedError } from 'rxjs';

@Component
export default class Modify extends Vue {

  select = "create";

  selectedId: string = "";
  front: string = "";
  back: string = "";
  period: number = 0;

  private storage = storage;

  submit(): void {
      if (this.select === 'create') {
          if (this.front.trim().split('').length <= 0) { 
              return;
          }
          if (this.front.trim().split('').length <= 0) {
              return;
          }
          this.storage.createCard({
              front: this.front,
              back: this.back
          }).then(() => this.$emit('created'));
      } else if (this.select === 'update') {
          if (this.selectedId.trim().split('').length <= 0) {
              return;
          }
          this.storage.updateCard(this.selectedId, {
              front: this.front,
              back: this.back,
              period: this.period
          })
      }
  }

  loadCard(): void {
      this.storage.getCard(this.selectedId)
        .then(card => {
            this.selectedId = card.id;
            this.front = card.front;
            this.back = card.back;
            this.period = card.period;
        });
  }

}
