import * as firebase from 'firebase/app';
import 'firebase/database';
import 'firebase/auth';
import { Observable, Observer } from 'rxjs';

import { ENV } from '../../../env';

export interface Card {
    id: string;
    front: string;
    back: string;
    period: number;
}

export interface CreateCard {
    front: string;
    back: string;
}

export interface CardChanges {
    front?: string;
    back?: string;
    period?: number;
}

export class Storage {

    private config = {
        apiKey: ENV.apiKey,
        authDomain: ENV.authDomain,
        databaseURL: ENV.databaseURL,
        projectId: ENV.projectId,
        storageBucket: ENV.storageBucket,
        messagingSenderId: ENV.messagingSenderId,
    };

    private app: firebase.app.App;
    private database: firebase.database.Database;
    private auth: firebase.auth.Auth;
    private authProvider: firebase.auth.GithubAuthProvider;

    private cardRef: firebase.database.Reference;

    private cardChange$?: Observable<Card[]>;

    constructor() {
        this.app = firebase.initializeApp(this.config);
        this.auth = this.app.auth();
        this.authProvider = new firebase.auth.GithubAuthProvider();
        this.database = this.app.database();
        this.cardRef = this.database.ref('/card');
    }

    public onCardChange$(): Observable<Card[]> {
        if (!this.cardChange$) {
            this.cardChange$ = Observable.create((observer: Observer<Card[]>) => {
                this.cardRef.on('value', (snapshot) => {
                    if (snapshot && snapshot.val()) {
                        observer.next(createCardsFromSnapshot(snapshot));
                    }
                });
            });
        }
        return this.cardChange$ as Observable<Card[]>;
    }

    public updateCard(id: string, changes: CardChanges): Promise<void> {
        return new Promise((resolve, reject) => {
        this.database.ref(`card/${id.trim()}`)
            .set(changes, (error) => {
                if (error) {
                    reject(error)
                } else {
                    resolve();
                }
            });
        });
    }

    public getCard(id: string): Promise<Card> {
        return this.database.ref(`card/${id.trim()}`).once('value')
            .then((snapshot) => ({
                id,
                ...snapshot.val()
            }));
    }

    public createCard(card: CreateCard): Promise<Card> {
        const key = this.database.ref().child('cards').push().key;

        if (!key) {
            return Promise.reject('No key generated');
        }

        return new Promise((resolve, reject) => {
            this.database.ref(`card/${key}`)
                .set({
                    period: 0,
                    ...card,
                }, (error) => {
                    if (error) {
                        reject(error);
                    } else {
                        resolve ({
                            ...card,
                            id: key,
                            period: 0,
                        });
                    }
                });
        });
    }

    public deleteCard(key: string): Promise<void> {
        return new Promise((resolve, reject) => {
            this.database.ref(`card/${key}`).remove((error) => {
                if (error) {
                    reject(error);
                } else {
                    resolve();
                }
            });
        });
    }

    public getCards(): Promise<Card[]> {
        return this.cardRef.once('value')
            .then((snapshot) => createCardsFromSnapshot(snapshot));
    }

    public requestAuth(): Promise<any> {
        return this.auth.signInWithPopup(this.authProvider);
    }

}

function createCardsFromSnapshot(snapshot: firebase.database.DataSnapshot): Card[] {
    const c = snapshot.val();
    if (c === null) {
        return [];
    }
    const cards = Object.keys(c);
    if (cards.length <= 0) {
        return [];
    }
    return cards
        .map((id) => ({
            id,
            ...c[id],
        }));
}